	module.exports = {"data":{"wordpressPost":{"title":"Nicole Kidman Wins Best Beauty Look At The 2017 Emmys","content":"<p>This year&#8217;s Emmy&#8217;s were quite possibly the best ever. The men were handsome, the women heavenly and none more so than our <strong>Nicole Kidman</strong> who, (as far as we&#8217;re concerned anyway), won <strong>Best Beauty Look</strong>.</p>\n<p>Okay, so there is no Emmys award for best beauty &#8230; But if there was she would have owned that thing! <strong>Jade Dowling</strong>, Senior Beauty Therapist at <a href=\"https://www.ry.com.au\">RY</a>, agrees so clearly we&#8217;re onto something.</p>\n<p>We put it to Jade &#8230; What was so bloody amazing about Nicole&#8217;s makeup on the night. And you might be surprised by her answer. Turns out Nicole broke a cardinal beauty rule &#8211; only she made it work. That rule? Matching your lipstick to your outfit which apparently is just not done.</p>\n<figure id=\"attachment_3164\" style=\"width: 262px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3164\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20205924/Hollywoodciak-JPG-262x300.jpg\" alt=\"\" width=\"262\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20205924/Hollywoodciak-JPG-262x300.jpg 262w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20205924/Hollywoodciak-JPG.jpg 516w\" sizes=\"(max-width: 262px) 100vw, 262px\" /><figcaption class=\"wp-caption-text\">Lady in red. Image: Instagram/@Hollywoodciak</figcaption></figure>\n<p>Except this time it was. And it ROCKED, says Jade.</p>\n<p>&#8220;Normally I would advise against trying to match a red lip to your red dress as it can easily look OTT but Nicole Kidman absolutely nails it,&#8221; she says.</p>\n<p>&#8220;Why? Because the look plays up her utterly flawless and iridescent skin. That and she’s opted for a very soft face-framing up do that brings the intensity of colour choice down a notch.&#8221;</p>\n<p>Fancy giving it a try yourself? Here&#8217;s how, according to Jade.</p>\n<p>&#8220;Nicole&#8217;s Emmy look can be reproduced,&#8221; she says.</p>\n<p>&#8220;You&#8217;ll need a quality primer and then, matte-based powders that reduce shine.&#8221;</p>\n<p>Jade recommends <strong>Youngblood Pressed Mineral Foundation, $79.95</strong>.</p>\n<p>Thick curtain veil lashes finish the look here. For the right level of intensity, Jade suggests re-usable lashes from <strong>Lash Me Lashes Elizabeth Eyelashes, $7.65,</strong> topped off with a volumizing mascara. She likes <strong>Mirenesse iCurl Secret Weapon 24Hr Mascara, $39.95.</strong></p>\n<p>Finally, that lip.</p>\n<p>&nbsp;</p>\n<img class=\"size-medium wp-image-3165 alignnone\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20210750/Berry-Formidable-171x300.jpg\" alt=\"\" width=\"171\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20210750/Berry-Formidable-171x300.jpg 171w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20210750/Berry-Formidable.jpg 551w\" sizes=\"(max-width: 171px) 100vw, 171px\" />\n<p>For that we like <strong>Bourjois Rogue Velvet Lipstick in Berry Formidable, $15</strong>. It is the most perfectly flattering colour, not to mention texture &#8211; finally, a long-lasting matte that never feels tight or uncomfortable no matter how long you wear it.</p>\n<img class=\"alignnone size-medium wp-image-3168\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20211906/Ecstasy_Shine_300_L9810900_3614271432865_RVB_3000-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20211906/Ecstasy_Shine_300_L9810900_3614271432865_RVB_3000-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20211906/Ecstasy_Shine_300_L9810900_3614271432865_RVB_3000-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20211906/Ecstasy_Shine_300_L9810900_3614271432865_RVB_3000-768x768.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20211906/Ecstasy_Shine_300_L9810900_3614271432865_RVB_3000-1024x1024.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>For those who aren&#8217;t too keen on matte lipsticks, we recommend <strong>Giorgio Armani Ecstasy Shine in 300, $56. </strong>Not sure how they did it, but this clever little number feels like a lacquer, has the colour of a lipstick and the hold of a stain. #Winning</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/20204115/flowerhouse.publicite-JPG.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"148a234a-b2de-5e71-840d-a19cba16080d"}}

